input[type=range] {
	-webkit-appearance: none;
	width: 250px;
	margin: 5.25px 0;
 }
 input[type=range]:focus {
	outline: none;
 }
 input[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
	background: #4c4d4d;
	border-radius: 25px;
	border: 0px solid #000101;
 }
 input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -5.25px;
 }
 input[type=range]:focus::-webkit-slider-runnable-track {
	background: #595a5a;
 }
 input[type=range]::-moz-range-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
	background: #4c4d4d;
	border-radius: 25px;
	border: 0px solid #000101;
 }
 input[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
 }
 input[type=range]::-ms-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
 }
 input[type=range]::-ms-fill-lower {
	background: #3f4040;
	border: 0px solid #000101;
	border-radius: 50px;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
 }
 input[type=range]::-ms-fill-upper {
	background: #4c4d4d;
	border: 0px solid #000101;
	border-radius: 50px;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
 }
 input[type=range]::-ms-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
	height: 1.5px;
 }
 input[type=range]:focus::-ms-fill-lower {
	background: #4c4d4d;
 }
 input[type=range]:focus::-ms-fill-upper {
	background: #595a5a;
 }
 
